
@import url('https://fonts.googleapis.com/css2?family=Gentium+Plus&display=swap');


.home-page .solid-logo {
    width: 435px !important;
    height: 556px;
    position: absolute;
    font-family: "-moz-initial";
    top: 15%;
    right: auto;
    bottom: auto;
    right: 15%;
    border: 10px;
    border-radius: 100%;
}
.home-page .text-zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 40%;
    max-height: 90%;
    font-family: "-moz-initial";
}
.home-page h1 {
    font-family: "-moz-initial";
    color: #fff;
    font-size: 56px;
    line-height: 53px;
    margin: 0;
    font-weight: 400;
}



.home-page h1 img {
    margin-left: 20px;
    opacity: 0;
    width: 40px;
    height: 65px;
    animation: rotateIn 1s linear both;
    animation-delay: 1.4s;
}
.home-page h2 {
    color: #8d8d8d;
    margin-top: 20px;
    font-weight: 400;
    font-size: 11px;
    font-family: "-moz-initial";
    letter-spacing: 3px;
    animation: fadeIn 1s 1.8s backwards;
}
.home-page .flat-button {
    color: #03fbff;;
    font-size: 13px;
    font-weight: 400;
    letter-spacing: 4px;
    font-family: "-moz-initial";
    text-decoration: none;
    padding: 10px 18px;
    border: 1px solid #03fbff;
    margin-top: 25px;
    float: left;
    animation: fadeInAnimation 1s 1.8s backwards;
    white-space: nowrap;
}
.home-page .flat-button:hover {
    background: #03fbff;;
    color: #333;
}
.contact-button{
    text-decoration: none;
    width: 100%;
    max-width: 200px;
    height: 40px;
    display: block;
    line-height: 40px;
    letter-spacing: 3px;
    position: relative;
    text-align: center;
    color: #08fdd8;
    margin-top: 50px;
}
.contact-button div{
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition:  all .3s ease-in-out;
}
.contact-button span.bg{
    position: absolute;
    width: 0%;
    height: 100%;
    left: -5px;
    top: 0;
    transition: all .3s ease-in-out;
    background-color: #08fdd8;
    transform: skewX(-19deg);
    
}
.contact-button:hover span.bg{
    width: 110%;
}
.contact-button span.base{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    border: 1px solid #08fdd8;
    box-sizing: border-box;
    
}
.contact-button span.text{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    
}
.contact-button:hover span.text{
    color: #1d1d1d;
}
