.skill-page{
    position: absolute;
    top: 30%;
    left: 10%;
    width: 40%;
    color: white;
    opacity: 0;
    animation: fadeIn 1s forwards;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
    -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        will-change: contents;
        z-index: 1;
}


.skillbarcontainer{
    height: fit-content;
    position: absolute;
    top: 20%;
    left: 50%;
    width: 50%;
    animation: backInRight 1s ;
    animation-fill-mode: forwards;
}